
.savollar{
    max-height:700px ;
    background-size: cover;
    border-radius: .4em;

}
.savollar li{
    cursor: pointer;
    list-style-type: upper-alpha;
    text-align: left;

}
.savollar li:hover{
    background: dodgerblue;
    border-radius: .5em;
    transform: scale(1.05);
}
span{
    margin: auto;
    margin-top:0 ;
    text-align: center;
    cursor: pointer;
    font-size: 2em;
}
span:hover{
    transform: scale(1.03);
    color: dodgerblue;
    text-shadow: 0 0 4px gray;
}

.dot {
    cursor: pointer;
    height: 15px;
    width: 15px;
    margin: 0 2px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
    transition: background-color 0.6s ease;
}

.zzz{
    position: relative;
    top: -1.5em;

}
.savol{
    font-size: 1.3em;
    margin-top: 3vh;
}
.active{
    background: black;
}

.close{
    position: relative;
    right: 3px;
    cursor: pointer;
    top: 3px;
}
.javoblar{
    position: fixed;
    width: 30%;
    background: dodgerblue;
    color: red;
    left: 35%;
    top: 15vh;
    border-radius: .5em;
    box-shadow: 0 0 5px red,0 0 2px blue ;
}
.javoblar li{
    list-style-type: upper-roman;
    border: orange solid;
    border-radius: 15px;
    margin: 5px;
}
.showAns{
    cursor: pointer;
    border-radius: 10%;
    background: rgba(197,99,249,0.97);
    color: white;

}

.showAns:hover{
    border: red dashed;
    transform: scale(1.02);
}
.StopButton{
    text-align: center;
    color: red;
    background: linear-gradient(35deg, rgba(249,74,136,0.97),rgba(174,249,108,0.97),rgba(139,152,249,0.97));
    border: dodgerblue  ;
    height: 3em;
    border-radius: 5px;
    font-size: 1.2em;
    width: 30%;
    margin: auto;
    margin-top: 2vh;
}
.natija{
    position: fixed;
    width: 50%;
    background: dodgerblue;
    color: red;
    left: 25%;
    top: 20vh;
    border-radius: .5em;
    box-shadow: 0 0 5px red,0 0 2px blue ;
    z-index: 998;
}

.none{
    display: none;
}

.test{
    max-width: 100vw;
    background: rgba(191,231,249,0.5);
    margin-top:10vh;
    padding: 1.3em;
    text-shadow: 0 0 2px red ;
    color: white;
    transition: 1s;

}