.container{
    background: linear-gradient(45deg , rgba(161,26,249,0.97),rgba(223,62,249,0.97), rgba(249,51,194,0.97),rgba(249,64,131,0.97)) ;
    min-height:100vh ;
}
.navbar{
    position: fixed;

    background: linear-gradient(90deg , rgb(249,207,200), rgb(135,249,205),rgb(219,249,107));
    height: 15vh;
    top: -1px;
    font-size: 2.5em;
    text-align: center;
    text-shadow: 1px 0px 5px red;
    color: rgba(249,33,0,0.9);
    width: 100%;
    z-index: 999;
}
.Container{
    min-height: 100vh;
    background: radial-gradient(circle, rgba(249,245,132,0.97),rgba(214,249,126,0.97),rgba(215,232,249,0.97));
    padding:5% ;
    width: 70%;
    text-align: center;
    box-shadow: 0 0 4px black,0 0 6px yellow;
    margin: auto;
}

.StartButton {
    position: absolute;
    width: 40vw;
    text-align: center;
    color: red;
    background: linear-gradient(35deg, rgba(249,74,136,0.97),rgba(174,249,108,0.97),rgba(139,152,249,0.97));
    border: dodgerblue  ;
    height: 3em;
    border-radius: 5px;
    font-size: 1.2em;
    top: 40vh;
    left: 30vw;

}

button:hover{
    transform: scale(1.09);
}
button{
    cursor: pointer;
}

@media screen and (max-width: 600px){
    .navbar{
        padding-top: 1em;
        font-size: 1em;
        height: 3em;
    }
    .Container{
        width: 90%;
    }
}